/*--------------------------------------------------------------
# Columns
--------------------------------------------------------------*/
.columns {
    --w-100: 100%;
    --w-50: 50%;
    --w-33: 33.333333%;
    --w-25: 25%;
    --w-20: 20%;
    --w-16: 16.666666%;

    --v1: 0 0 var(--w-100);
    --v2: 0 0 var(--w-50);
    --v3: 0 0 var(--w-33);
    --v4: 0 0 var(--w-25);
    --v5: 0 0 var(--w-20);
    --v6: 0 0 var(--w-16);

    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    row-gap: 30px;
}

.columns>.column {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
}

.columns[data-columns="1"]>.column {
    -ms-flex: var(--v1);
    -webkit-box-flex: var(--v1);
    flex: var(--v1);
    max-width: var(--w-100);
}

.columns[data-columns="1"]>.components-card__body.column {
    max-width: calc(var(--w-100) - 26px) !important;
}

components-card__body .columns[data-columns="2"]>.column {
    -ms-flex: var(--v2);
    -webkit-box-flex: var(--v2);
    flex: var(--v2);
    max-width: var(--w-50);
}

.columns[data-columns="3"]>.column {
    -ms-flex: var(--v3);
    -webkit-box-flex: var(--v3);
    flex: var(--v3);
    max-width: var(--w-33);
}

.columns[data-columns="4"]>.column {
    -ms-flex: var(--v4);
    -webkit-box-flex: var(--v4);
    flex: var(--v4);
    max-width: var(--w-25);
}

.columns[data-columns="5"]>.column {
    -ms-flex: var(--v5);
    -webkit-box-flex: var(--v5);
    flex: var(--v5);
    max-width: var(--w-20);
}

.columns[data-columns="6"]>.column {
    -ms-flex: var(--v6);
    -webkit-box-flex: var(--v6);
    flex: var(--v6);
    max-width: var(--w-16);
}

@media only screen and (min-width: 720px) {
    .columns[data-columns-md="1"]>.column {
        -ms-flex: var(--v1);
        -webkit-box-flex: var(--v1);
        flex: var(--v1);
        max-width: var(--w-100);
    }

    .columns[data-columns-md="2"]>.column {
        -ms-flex: var(--v2);
        -webkit-box-flex: var(--v2);
        flex: var(--v2);
        max-width: var(--w-50);
    }

    .columns[data-columns-md="2"]>.components-card__body.column {
        max-width: calc(var(--w-50) - 26px) !important;
    }

    .columns[data-columns-md="3"]>.column {
        -ms-flex: var(--v3);
        -webkit-box-flex: var(--v3);
        flex: var(--v3);
        max-width: var(--w-33);
    }

    .columns[data-columns-md="4"]>.column {
        -ms-flex: var(--v4);
        -webkit-box-flex: var(--v4);
        flex: var(--v4);
        max-width: var(--w-25);
    }

    .columns[data-columns-md="5"]>.column {
        -ms-flex: var(--v5);
        -webkit-box-flex: var(--v5);
        flex: var(--v5);
        max-width: var(--w-20);
    }

    .columns[data-columns-md="6"]>.column {
        -ms-flex: var(--v6);
        -webkit-box-flex: var(--v6);
        flex: var(--v6);
        max-width: var(--w-16);
    }
}

@media only screen and (min-width: 1024px) {
    .columns[data-columns-lg="1"]>.column {
        -ms-flex: var(--v1);
        -webkit-box-flex: var(--v1);
        flex: var(--v1);
        max-width: var(--w-100);
    }

    .columns[data-columns-lg="2"]>.column {
        -ms-flex: var(--v2);
        -webkit-box-flex: var(--v2);
        flex: var(--v2);
        max-width: var(--w-50);
    }

    .columns[data-columns-lg="3"]>.column {
        -ms-flex: var(--v3);
